@import 'normalize.css';


body {
    font-family: 'Poppins', sans-serif;
    margin: 20px;
}

.date-time {
    font-size: smaller;
    color: #635f5d;
    margin-left: auto;
    margin-right: auto;
    max-width: 1080px;
}

.page-container {
    margin-left: auto;
    margin-right: auto;
    max-width: 1080px;
}


.subtitle {
    padding-top: 20px;
}

.election-summary-table {
    border: 2px solid #c0c0bb;
    padding: 1rem;
    border-radius: 10px;
}

.election-summary-table td {
    text-align: left;
}

td {
    text-align: center;
}

.result-summary-table {
    border: 2px solid #c0c0bb;
    padding: 1rem;
    border-radius: 10px;
}

.text-content {
    margin-top: 0px;
    margin-right: auto;
    margin-bottom: 24px;
    margin-left: auto;
}

.text-content-small {
    font-size: smaller;
    margin-top: 0px;
    margin-right: auto;
    margin-bottom: 24px;
    margin-left: auto;
}
.winner-party {
    color: #800000;
    padding-left: 10px;
    padding-right: 5px;
}



.bar-chart-container {
    display: flex;
    justify-content: center;
}


.geo-chart-container {
    display: flex;
    justify-content:left;
    align-items: stretch;
}


.tick line {
    stroke: #e0dddb;
    stroke-width: 1px;
}

.tick text {
    fill: #635f5d;
}

.total-votes {
    fill: #E5E2E0;
    cursor: pointer;
}

.total-polled-votes {
    fill: #42A5B3;
}

.chart-label {
    font-size: 1.2em;
}

.select-option-container {
    display: flex;
    justify-content: left;
    align-items: center;
}

.select-chart {
    display: flex;
    width: 90%;
    margin: auto;
    align-items: center;
}
label {
    margin-left: 10px;
    margin-right: 20px;
}

.margin-tb {
    margin-top: 10px;
    margin-bottom: 10px;
}
select {
    margin: 10px;
    font-family: 'Poppins', sans-serif;
}

.wrapper {
    position: relative;
}

.bar-chart-tooltip {
    opacity: 1.0;
    position: absolute;
    top: 0;
    left: 0;
    padding: 0.6em 1em;
    background: #fff;
    text-align: center;
    line-height: 1.4em;
    font-size: 0.8em;
    z-index: 10;
    transition: all 0.5s ease-out;
    border: 1px solid #c0c0bb;
    border-radius: 10px;
}

.bar-chart-tooltip::before {
    content: '';
    position: absolute;
    top: 15px;
    left: -7.0px;
    width: 12px;
    height: 12px;
    background: white;
    transform: rotate(45deg);
    border-bottom:1px solid #c0c0bb;
    border-left:1px solid #c0c0bb;
    z-index: 10;
    transition: all 0.5s ease-out;
}

.table-head-name {
    border-bottom:1px solid #e0dddb;
    font-size: 1.5em;
}
.table-head-line {
    border-bottom:1px solid #e0dddb;
}
.table-head {
    font-size: 1.2em;
}
table td {
    padding: 2px;
}

.geo-path {
    stroke: #c0c0bb;
    stroke-width: 1px;
}

.winners-map {
    stroke: #c0c0bb;
    stroke-width: 1px;
}

.votes-map {
    stroke: #c0c0bb;
    stroke-width: 1px;
}

.winners-map .land:hover{
    fill: #c0c0bb;
}

.votes-map .land:hover{
    fill-opacity: 0.2;
}

.parties-map .land:hover{
    fill: #c0c0bb;
    /* fill-opacity: 0.8; */
}

.geo-path .interiors{
    fill: none;
    stroke: #c0c0bb;
}

.geo-path .graticule{
    fill: none;
    stroke: #dadada;
}
.geo-path .sphere{
    fill: #ececec;
}

path title:hover {
    font-family: 'Poppins', sans-serif;
}

hr {
    stroke: #e0dddb;
    stroke-width: 1px;
}

